import React, { useEffect, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useRouter } from 'next/router';
import Head from 'next/head';
import PageSwapper from '@moxy/react-page-swapper';
import ThemeProvider from '../components/theme-provider';
import { PageTransition, AnimationPicker } from '../components';
import createScrollBehavior from '../components/scroll-behavior';
import './_global.css';
import styles from './_app.module.css';

const getNextHref = (pathname) => {
    switch (pathname) {
    case '/': return '/page2';
    case '/page2': return '/page3';
    default: return '/';
    }
};

const App = ({ Component, pageProps }) => {
    useEffect(() => {
        const jssStyles = document.querySelector('#jss-server-side');

        if (jssStyles) {
            jssStyles.parentElement.removeChild(jssStyles);
        }
    }, []);

    const router = useRouter();
    const scrollBehavior = useMemo(() => createScrollBehavior(router), []);

    return (
        <>
            <Head>
                <title>@moxy/react-page-swapper demo</title>
                <meta name="viewport" content="minimum-scale=1, initial-scale=1, width=device-width" />
            </Head>

            <ThemeProvider>
                <PageSwapper
                    updateScroll={ () => scrollBehavior.updateScroll() }
                    className={ styles.pageSwapper }
                    node={ <Component { ...pageProps } /> }
                    animation={ router.query.animation ?? 'none' }>
                    { (props) => <PageTransition { ...props } /> }
                </PageSwapper>

                <AnimationPicker nextHref={ getNextHref(router.pathname) } />
            </ThemeProvider>
        </>
    );
};

App.propTypes = {
    Component: PropTypes.elementType.isRequired,
    pageProps: PropTypes.object.isRequired,
};

export default App;
